.new-design {
  
  .block-inner {
    // .block-inner__slider

    &__slider {
    }

    // .block-inner__wrapper

    &__wrapper {
    }

    // .block-inner__slide

    &__slide {
      display: flex;
      height: auto;
      position: relative;
      padding: 120px 0px;
      @media (max-width: 768px) {
        padding: 60px 0px;
      }
      &::before {
        content: "";
        background: linear-gradient(47deg, rgba(17, 17, 17, 0.3) 41.77%, rgba(119, 119, 119, 0.3) 100%);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 3;
      }
      &::after {
        content: "";
        background: rgba(17, 17, 17, 0.3);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 4;
      }
      &.slide1 {
        &::before {
          content: "";
          background: linear-gradient(113deg, rgba(119, 119, 119, 0.3) 0%, rgba(17, 17, 17, 0.3) 100%);
        }
      }
      &.slide2 {
        .block-inner__left {
          flex: 0 1 863px;
          @media (max-width: 992px) {
            flex: 1 1 auto;
          }
        }
      }
      &.slide3 {
        .block-inner__left {
          flex: 0 1 924px;
          @media (max-width: 992px) {
            flex: 1 1 auto;
          }
        }
      }
    }

    &__bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    // .block-inner__body

    &__body {
      position: relative;
      z-index: 10;
      display: flex;
      justify-content: space-between;
      gap: 50px 30px;
      @media (max-width: 992px) {
        flex-direction: column;
        justify-content: start;
      }
      @media (max-width: 768px) {
        gap: 20px;
      }
      ul {
        flex: 0 1 437px;
        @media (max-width: 992px) {
          flex: 1 1 auto;
          max-width: 437px;
        }
        li {
          border-bottom: 1px solid rgba(255, 255, 255, 0.4);
          padding: 0px 0px 25px 0px;
          margin: 0px 0px 25px 0px;
          &:last-child {
            margin: 0px 0px 0px 0px;
            padding: 0px 0px 0px 0px;
            border-bottom: none;
          }
          @media (max-width: 768px) {
            margin: 0px 0px 10px 0px;
            padding: 0px 0px 10px 0px;
          }
        }
      }
    }

    // .block-inner__left

    &__left {
      flex: 0 1 781px;
      @media (max-width: 992px) {
        flex: 1 1 auto;
      }
      p {
        font-weight: 350;
        font-size: 20px;
        line-height: 160%;
        color: #fff;
        margin: 40px 0px 0px 0px;
        @media (max-width: 768px) {
          margin: 20px 0px 0px 0px;
          font-size: 16px;
        }
      }
    }

    // .block-inner__right

    &__right {
      flex: 0 1 287px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      gap: 20px;
      @media (max-width: 992px) {
        align-items: start;
      }
      p {
        display: flex;
        flex-direction: column;
        flex: 1 1 auto;
        font-weight: 350;
        font-size: 20px;
        line-height: 160%;
        color: #fff;
        a {
          text-decoration: underline;
          transition: opacity 0.3s;
          &:hover {
            opacity: 0.8;
          }
        }
        @media (max-width: 768px) {
          font-size: 16px;
        }
      }
      img {
        width: 45px;
        height: 45px;
        display: block;
        @media (max-width: 768px) {
          width: 35px;
          height: 35px;
        }
      }
      @media (max-width: 992px) {
        flex: 1 1 auto;
        max-width: 287px;
      }
    }

    // .block-inner__name

    &__name {
      font-weight: 500;
      font-size: 20px;
      line-height: 160%;
      color: #fff;
      margin: 0px 0px 3px 0px;
      @media (max-width: 768px) {
        font-size: 16px;
      }
    }

    // .block-inner__subname

    &__subname {
      font-weight: 350;
      font-size: 15px;
      line-height: 160%;
      color: #fff;
      @media (max-width: 768px) {
        font-size: 13px;
      }
    }
  }

  .block-about {
    // .block-about__content

    &__content {
    }

    // .block-about__body

    &__body {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      @media (max-width: 992px) {
        flex-direction: column-reverse;
        justify-content: start;
      }
    }

    // .block-about__left

    &__left {
      flex: 0 1 674px;
      display: flex;
      flex-direction: column;
      gap: 40px;
      @media (max-width: 992px) {
        flex: 1 1 auto;
      }
      @media (max-width: 768px) {
        gap: 20px;
      }
      p {
        line-height: 160%;
        font-weight: 350;
        span {
          font-weight: 500;
        }
        @media (max-width: 768px) {
          font-size: 14px;
        }
      }
      ul {
        display: flex;
        flex-direction: column;
        gap: 20px 0px;
        @media (max-width: 768px) {
          gap: 10px 0px;
        }
        li {
          display: flex;
          align-items: start;
          img {
            width: 20px;
            height: 18px;
            margin: 5px 19px 0px 4px;
            @media (max-width: 768px) {
              width: 18px;
              height: 14px;
              margin: 5px 10px 0px 0px;
            }
          }
        }
      }
    }

    // .block-about__image

    &__image {
      flex: 0 1 600px;
      @media (max-width: 992px) {
        flex: 1 1 auto;
        display: flex;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  .block-work {
    // .block-work__content

    &__content {
    }

    // .block-work__body

    &__body {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 30px;
      @media (max-width: 992px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 768px) {
        gap: 15px;
      }
      @media (max-width: 480px) {
        grid-template-columns: 1fr;
      }
    }

    // .block-work__column

    &__column {
      display: flex;
      flex-direction: column;
      height: 100%;
      background-color: var(--theme-color-grey);
      p {
        flex: 1 1 auto;
        padding: 24px;
        line-height: 160%;
        color: #111;
        @media (max-width: 768px) {
          font-size: 14px;
          padding: 15px;
        }
      }
    }

    // .block-work__image

    &__image {
      position: relative;
      height: 118px;
      @media (max-width: 768px) {
        height: 100px;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    // .block-work__number

    &__number {
      display: flex;
      align-items: center;
      position: absolute;
      bottom: 24px;
      left: 24px;
      width: calc(100% - 24px);
      @media (max-width: 768px) {
        bottom: 15px;
        left: 15px;
        width: calc(100% - 15px);
      }
      span {
        flex: 0 0 40px;
        background: #fff;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        font-weight: 500;
        font-size: 14px;
        line-height: 160%;
        text-transform: uppercase;
        text-align: center;
        color: var(--theme-color-accent);
        display: flex;
        justify-content: center;
        align-items: center;
        @media (max-width: 768px) {
          flex: 0 0 30px;
          width: 30px;
          height: 30px;
          font-size: 12px;
        }
      }
      &::after {
        content: "";
        width: 100%;
        border: 1px dashed rgba(255, 255, 255, 0.5);
        margin: 0px 0px 0px 16px;
      }
    }
  }

  .block-projects {
    padding: 120px 0px;
    background-color: var(--theme-color-accent);
    @media (max-width: 768px) {
      padding: 60px 0px;
    }
    .heading-complex1 {
      color: #fff;
      border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    }

    // .block-projects__content

    &__content {
      .container {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }

    // .block-projects__body

    &__body {
      width: 100%;
      display: grid;
      gap: 30px;
      grid-template-columns: repeat(3, 1fr);
      @media (max-width: 1200px) {
        gap: 20px;
      }
      @media (max-width: 992px) {
        grid-template-columns: 1fr;
      }
    }

    // .block-projects__column

    &__column {
      height: 100%;
      display: flex;
      flex-direction: column;
      color: #fff;
      transition:
        background-color 0.3s ease,
        color 0.3s ease;

      &.item-active {
        background-color: #fff;
        color: var(--theme-color-text-common);
        ul {
          li {
            span {
              color: #7c7c7c;
            }
          }
        }
        .block-projects__bottom {
          opacity: 1;
        }
      }
      &:hover {
        background-color: #fff;
        color: var(--theme-color-text-common);
        ul {
          li {
            span {
              color: #7c7c7c;
            }
          }
        }
        .block-projects__bottom {
          opacity: 1;
        }
      }
    }

    // .block-projects__image

    &__image {
      display: block;
      height: 261px;
      width: 100%;
      object-fit: cover;
      @media (max-width: 480px) {
        height: 200px;
      }
    }

    // .block-projects__item

    &__item {
      display: flex;
      flex-direction: column;
      height: 100%;
      padding: 24px;
      @media (max-width: 1200px) {
        padding: 15px;
      }
      @media (max-width: 992px) {
        height: auto;
      }
      ul {
        flex: 1 1 auto;
        display: flex;
        flex-direction: column;
        gap: 4px;
        li {
          font-weight: 350;
          font-size: 15px;
          line-height: 150%;
          @media (max-width: 768px) {
            font-size: 13px;
          }
          span {
            transition: color 0.3s ease;
            color: rgba(255, 255, 255, 0.6);
          }
        }
      }
    }

    // .block-projects__title

    &__title {
      margin: 0px 0px 10px 0px;
    }

    // .block-projects__bottom

    &__bottom {
      opacity: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      margin: 24px 0px 0px 0px;
      transition: opacity 0.3s ease;
      @media (max-width: 768px) {
        margin: 15px 0px 0px 0px;
      }
    }

    // .block-projects__location

    &__location {
      flex: 1 1 auto;
      display: flex;
      align-items: center;
      font-weight: 700;
      font-size: 14px;
      line-height: 160%;
      text-transform: uppercase;
      text-align: center;
      color: #111;
      img {
        width: 18px;
        height: 18px;
        margin: 0px 6px 0px 0px;
      }
    }

    // .block-projects__arrow

    &__arrow {
      width: 34px;
      height: 34px;
    }

    // .block-projects__button

    &__button {
      margin: 60px 0px 0px 0px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #fff;
      border-radius: 200px;
      padding: 18px 28px 17px 28px;
      font-weight: 700;
      font-size: 14px;
      line-height: 160%;
      text-transform: uppercase;
      text-align: center;
      color: #fff;
      transition: opacity 0.3s;
      &:hover {
        opacity: 0.7;
      }
      @media (max-width: 768px) {
        margin: 30px 0px 0px 0px;
        padding: 10px 28px;
      }
      @media (max-width: 480px) {
        width: 100%;
      }
    }
  }

  .block-solutions {
    // .block-solutions__content

    &__content {
    }

    // .block-solutions__body

    &__body {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 30px;
      @media (max-width: 1200px) {
        gap: 20px;
      }
      @media (max-width: 992px) {
        grid-template-columns: repeat(2, 1fr);
        gap: 15px;
      }
      @media (max-width: 480px) {
        grid-template-columns: 1fr;
      }
      .card-item {
        &:hover {
          background-color: var(--theme-color-accent);
          img {
            filter: brightness(0) invert(1);
          }
          .card-item__title {
            color: #fff;
          }
          .card-item__bottom {
            p {
              color: #fff;
            }
          }
        }
      }
    }
  }

  .block-equipment {
    padding: 120px 0px;
    background-color: var(--theme-color-grey);
    @media (max-width: 768px) {
      padding: 60px 0px;
    }
    .heading-complex1 {
      padding: 0px 0px 44px 0px;
      @media (max-width: 768px) {
        padding: 0px 0px 20px 0px;
      }
      .container {
        display: flex;
        @media (max-width: 992px) {
          flex-direction: column;
        }
      }
    }
    // .block-equipment__content

    &__content {
      padding: 10px 0px 0px 0px;
      @media (max-width: 768px) {
        padding: 0px 0px 0px 0px;
      }
    }
  }
  .tabs {
    display: flex;
    flex-direction: column;
    gap: 30px 0px;
    @media (max-width: 768px) {
      gap: 15px;
    }
    // .tabs__navigation

    &__navigation {
      background: #fff;
      padding: 40px;
      @media (max-width: 1400px) {
        margin: 0px -15px;
      }
      @media (max-width: 768px) {
        padding: 15px;
      }
    }

    // .tabs__name

    &__name {
      font-weight: 500;
      font-size: 25px;
      line-height: 130%;
      color: #111;
      margin: 0px 0px 30px 0px;
      @media (max-width: 768px) {
        font-size: 20px;
        margin: 0px 0px 15px 0px;
      }
      @media (max-width: 480px) {
        font-size: 16px;
      }
    }

    // .tabs__titles

    &__titles {
      display: flex;
      flex-wrap: nowrap;
      margin: 0px -40px;
      padding: 0px 40px;
      gap: 30px;
      overflow: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        display: none;
      }
      @media (max-width: 768px) {
        margin: 0px -15px;
        padding: 0px 15px;
        gap: 15px;
      }
    }

    // .tabs__title

    &__title {
      flex: 1 1 234px;
      min-width: 234px;
      position: relative;
      color: #111;
      padding: 24px 4px 20px 24px;
      background-color: var(--theme-color-grey);
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      transition:
        background-color 0.3s,
        color 0.3s;
      @media (max-width: 768px) {
        flex: 0 0 180px;
        min-width: 180px;
        padding: 10px 4px 10px 10px;
      }
      span {
        font-weight: 500;
        font-size: 20px;
        line-height: 160%;
        width: 100%;
        text-align: left;
        display: block;
        @media (max-width: 768px) {
          font-size: 16px;
        }
        @media (max-width: 480px) {
          font-size: 14px;
        }
      }
      &._tab-active {
        background-color: var(--theme-color-accent);
        color: #fff;
        .tabs__arrow {
          opacity: 1;
        }
      }
      &:hover {
        background-color: var(--theme-color-accent);
        color: #fff;
        .tabs__arrow {
          opacity: 1;
        }
      }
    }

    // .tabs__arrow

    &__arrow {
      transition: opacity 0.3s ease;
      width: 34px;
      height: 34px;
      position: absolute;
      bottom: 24px;
      left: 24px;
      opacity: 0;
      @media (max-width: 768px) {
        left: 10px;
        bottom: 10px;
        width: 28px;
        height: 28px;
      }
    }

    // .tabs__image

    &__image {
      width: 150px;
      height: 150px;
      object-fit: contain;
      margin: 5px 0px 0px 0px;
      @media (max-width: 768px) {
        width: 100px;
        height: 100px;
      }
    }

    // .tabs__content

    &__content {
    }

    // .tabs__body

    &__body {
    }

    // .tabs__cards

    &__cards {
    }
  }
  .cards-tabs {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    @media (max-width: 1200px) {
      gap: 15px;
    }
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
    // .cards-tabs__column

    &__column {
      background: #fff;
      display: flex;
      align-items: center;
      transition: background-color 0.3s ease;
      &:hover {
        background-color: var(--theme-color-accent-80);
        .cards-tabs__title {
          color: #fff;
        }
        ul {
          li {
            color: #fff;
          }
        }
      }
      span {
        display: block;
        width: 2px;
        height: 81px;
        background-color: #efefef;
        @media (max-width: 480px) {
          width: 1px;
          height: calc(100% - 15px);
          margin: 15px 0px;
        }
      }
      img {
        width: 233px;
        height: 100%;
        object-fit: contain;
        @media (max-width: 1200px) {
          width: 180px;
        }
        @media (max-width: 480px) {
          width: 110px;
        }
      }
    }

    // .cards-tabs__item

    &__item {
      padding: 40px 30px 40px 30px;
      @media (max-width: 768px) {
        padding: 15px;
      }
      ul {
        li {
          transition: color 0.3s ease;
          display: flex;
          font-weight: 700;
          font-size: 14px;
          line-height: 160%;
          text-transform: uppercase;
          color: #7c7a7a;
          @media (max-width: 480px) {
            font-size: 12px;
          }
          &::before {
            content: "";
            width: 5px;
            height: 5px;
            background-color: var(--theme-color-accent);
            margin: 9px 7px 0px 0px;
            @media (max-width: 480px) {
              margin: 7px 7px 0px 0px;
            }
          }
        }
      }
    }

    // .cards-tabs__title

    &__title {
      transition: color 0.3s ease;
      font-weight: 500;
      font-size: 25px;
      line-height: 130%;
      color: #111;
      margin: 0px 0px 15px 0px;
      @media (max-width: 768px) {
        font-size: 20px;
        margin: 0px 0px 10px 0px;
      }
      @media (max-width: 480px) {
        font-size: 16px;
      }
    }
  }

  .block-services {
    padding: 120px 0px;
    background-color: var(--theme-color-grey);
    @media (max-width: 768px) {
      padding: 60px 0px;
    }
    .heading-complex1 {
      border-bottom: none;
      padding: 0px 0px 0px 0px;
    }
    // .block-services__content

    &__content {
    }

    // .block-services__body

    &__body {
      display: grid;
      gap: 28px 30px;
      grid-template-columns: repeat(2, 1fr);
      @media (max-width: 992px) {
        gap: 15px;
      }
      @media (max-width: 768px) {
        grid-template-columns: 1fr;
      }
      .card-item {
        background-color: #fff;
        min-height: 310px;
        @media (max-width: 768px) {
          min-height: 100%;
        }
      }
    }
  }

  .block-benefits {
    // .block-benefits__content

    &__content {
    }

    // .block-benefits__body

    &__body {
      display: flex;
      gap: 0px 30px;
      @media (max-width: 1200px) {
        gap: 0px 15px;
      }
      @media (max-width: 992px) {
        flex-direction: column;
        gap: 15px;
      }
    }

    // .block-benefits__row

    &__row {
      display: flex;
      flex-direction: column;
      gap: 30px 0px;
      flex: 0 1 627px;
      @media (max-width: 1200px) {
        gap: 15px 0px;
      }
      @media (max-width: 992px) {
        flex: 1 1 auto;
      }
      &:first-child {
        flex: 0 1 341px;
        @media (max-width: 992px) {
          flex: 1 1 auto;
        }
      }
      &:last-child {
        flex: 0 1 341px;
        @media (max-width: 992px) {
          flex: 1 1 auto;
        }
      }
      .card-item {
        flex: 1;
        gap: 20px 0px;
        height: auto;
        min-height: auto;
        justify-content: space-between;
      }
      .card-item__title {
        margin: 0px 0px 4px 0px;
      }
    }
  }

  .block-other-services {
    .heading-complex1 {
      padding: 0px 0px 23px 0px;
    }
    // .block-other-services__content

    &__content {
    }

    // .block-other-services__body

    &__body {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 28px 30px;
      @media (max-width: 1200px) {
        gap: 15px;
      }
      @media (max-width: 992px) {
        grid-template-columns: 1fr;
      }
      .card-item {
        flex-direction: row;
        padding: 0px 0px 0px 0px;
        min-height: 100%;
      }
    }
  }

  .block-reviews {
    // .block-reviews__content

    &__content {
    }

    // .block-reviews__body

    &__body {
    }

    // .block-reviews__slider

    &__slider {
      @media (max-width: 1400px) {
        margin: 0px -15px;
        padding: 0px 15px;
      }
    }

    // .block-reviews__wrapper

    &__wrapper {
    }

    // .block-reviews__slide

    &__slide {
      display: flex;
      height: auto;
    }

    // .block-reviews__item

    &__item {
      display: flex;
      @media (max-width: 480px) {
        flex-direction: column;
      }
    }

    // .block-reviews__image

    &__image {
      flex: 0 0 203px;
      background-color: var(--theme-color-grey);
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
      @media (max-width: 768px) {
        flex: 1 0 120px;
      }
      @media (max-width: 480px) {
        flex: 0 0 150px;
        height: 150px;
      }
    }

    // .block-reviews__text

    &__text {
      border: 1px solid #f7f7f7;
      padding: 24px 24px 30px 24px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      gap: 43px;
      @media (max-width: 768px) {
        padding: 15px;
        gap: 20px;
      }
      p {
        display: flex;
        flex-direction: column;
        font-weight: 350;
        font-size: 15px;
        line-height: 160%;
        color: #111;
        span {
          font-weight: 500;
        }
        @media (max-width: 768px) {
          font-size: 13px;
        }
      }
    }

    // .block-reviews__nav

    &__nav {
      margin: 40px 0px 0px 0px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 768px) {
        margin: 30px 0px 0px 0px;
      }
    }

    // .block-reviews__dotts

    &__dotts {
      width: 120px;
      display: flex;
      transition: background-color 0.3s;
      .swiper-pagination-bullet {
        transition: background-color 0.3s;
        cursor: pointer;
        flex: 1 1 auto;
        opacity: 1;
        height: 3px;
        border-radius: 0px;
        background-color: var(--theme-color-grey);
        &:hover {
          background-color: rgba(47, 128, 237, 0.5);
        }
      }
      .swiper-pagination-bullet-active {
        background-color: var(--theme-color-accent);
      }
    }

    // .block-reviews__fraction

    &__fraction {
      display: flex;
      align-items: center;
      gap: 27px;
      font-weight: 700;
      font-size: 14px;
      line-height: 160%;
      text-transform: uppercase;
      color: #111;
      @media (max-width: 768px) {
        gap: 15px;
        font-size: 12px;
      }
    }

    // .block-reviews__current

    &__current {
    }

    // .block-reviews__all

    &__all {
    }

    // .block-reviews__arrows

    &__arrows {
      display: flex;
      align-items: center;
      gap: 10px;
    }

    // .block-reviews__arrow-prev

    &__arrow-prev {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      background-color: var(--theme-color-accent);
      transition: background-color 0.3s;
      img {
        width: 18px;
        height: 12px;
        transform: rotate(-180deg);
      }
      &:hover {
        background-color: var(--theme-color-accent-80);
      }
      &.swiper-button-disabled {
        pointer-events: none;
        opacity: 0.8;
      }
      @media (max-width: 768px) {
        display: none;
      }
    }

    // .block-reviews__arrow-next

    &__arrow-next {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      background-color: var(--theme-color-accent);
      transition: background-color 0.3s;
      img {
        width: 18px;
        height: 12px;
      }
      &:hover {
        background-color: var(--theme-color-accent-80);
      }
      &.swiper-button-disabled {
        pointer-events: none;
        opacity: 0.8;
      }
      @media (max-width: 768px) {
        display: none;
      }
    }
  }

  .block-guarantee {
    // .block-guarantee__body

    &__body {
      display: flex;
      gap: 20px;
      margin: 0px 10px 0px 27px;
      @media (max-width: 992px) {
        flex-direction: column;
        margin: 0px 0px 0px 0px;
      }
      .title2 {
        flex: 1 1 55%;
        @media (max-width: 992px) {
          flex: 1 1 auto;
        }
      }
    }

    // .block-guarantee__right

    &__right {
      display: flex;
      flex: 0 1 51%;
      font-weight: 350;
      font-size: 18px;
      @media (max-width: 992px) {
        flex: 1 1 auto;
      }
      img {
        width: 60px;
        height: 60px;
        margin: 1px 30px 0px 0px;
        @media (max-width: 768px) {
          width: 40px;
          height: 40px;
          margin: 8px 20px 0px 0px;
        }
      }
      @media (max-width: 768px) {
        font-size: 14px;
      }
    }
  }
}
